.features {
  margin-top: 5px;
}

.features__title {
  position: relative;
  margin: 0;
  margin-bottom: 30px;
  font-weight: $fontBold;
  font-size: $fontHeadersH2;
  line-height: 1.2;

  &::before {
    content: "";
    position: absolute;
    top: -17px;
    left: 0;
    width: 124px;
    height: 4px;
    background-color: $ColorSecondary;
  }

  @media (min-width: $width4K) {
    font-size: $fontHeadersH2 * 1.5;

    &::before {
      top: -25px;
      width: 200px;
      height: 6px;
    }
  }
}

.features__slider {
  display: flex;
  margin-bottom: 25px;
}

.features__slide {
  width: 373px;
  height: 242px;
  margin-right: 28px;
  color: $ColorPrimary400;
  background: linear-gradient(167deg, $ColorPrimary300 0%, $ColorPrimary400 97%);
  border-radius: 10px;
  box-shadow: 0 10px 10px rgba($ColorPrimary300, 0.25);
  position: relative;
  cursor: pointer;
  transition:
    color 0.3s,
    box-shadow 0.3s;

  &:hover,
  &:focus {
    color: $ColorSecondary;
    background: linear-gradient(347deg, $ColorBlack -14%, currentColor 87%);
    outline: none;
    box-shadow: 0 5px 10px rgba($ColorSecondary, 0.25);

    .features__slide-number {
      color: $ColorWhite;
      opacity: 1;
    }

    .features__slide-title {
      color: $ColorPrimary;
    }

    .features__slide-image {
      filter: grayscale(0);
    }
  }

  @media (min-width: $width4K) {
    height: 277px;
    margin-right: 88px;
  }
}

.features__slide-number {
  display: inline-block;
  font-size: $fontHeadersH4;
  font-weight: $fontSemiBold;
  color: $fontColorLightGrey;
  line-height: 43px;
  opacity: 0.5;
  margin: 28px 0 0 22px;
  transition:
    color 0.3s,
    opacity 0.3s;
}

.features__slide-title {
  width: 165px;
  font-size: $fontTextSmall;
  font-weight: $fontBold;
  color: $fontColorDarkGrey;
  line-height: $lineHeightMedium;
  text-transform: uppercase;
  margin: 11px 0 41px 22px;
  transition: color 0.3s;

  @media (min-width: $width4K) {
    width: 250px;
    font-size: $fontTextSmall * 1.5;
    line-height: 1.2;
  }
}

.features__slide-text {
  display: inline-block;
  width: 212px;
  font-size: $fontTextSmall;
  font-weight: $fontLight;
  color: $fontColorGrey;
  line-height: $lineHeightSmall;
  margin: 0 0 44px 22px;

  @media (min-width: $width2K) {
    width: 65%;
    font-size: $fontTextRegular;
    line-height: 1.2;
  }

  @media (min-width: $width4K) {
    font-size: $fontTextMedium;
  }
}

.features__slide-image {
  position: absolute;
  top: 12px;
  right: 23px;
  filter: grayscale(1);
  transition: filter 0.3s;
}
